@import url('https://fonts.googleapis.com/css2?family=Manrope&display=swap');

.dataContainer {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.mainContainer {
  padding: 0 15%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  overflow-y: auto;
  height: 100vh;
  scroll-behavior: smooth;
}

.paymentContainer {
  margin-left: 32px;
  width: 40%;
  display: flex;
  flex-direction: column;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}


.generalTitle {
  text-align: center;
  margin-top: 24px;
}

.mt-12 {
  margin-top: 12px;
}

.row {
  display: flex;
  column-gap: 16px;
}
.row > * {
  margin-top: 16px;
}

p {
  
  font-family: Manrope, sans-serif;
}


@media (max-width: 1200px) {
  .dataContainer {
    width: 100%;
    display: flex;
    flex-direction: column;
  }

  .mainContainer {
    flex-direction: column;
    padding: 0 16px;
  }

  .paymentContainer {
    width: 100%;
    margin-left: 0;
  }

  .row {
    flex-direction: column;
  }
}
