.Switch {
    content: "";
    width: 32px;
    height: 32px;
    border: 2px solid black;
    cursor: pointer;
}
.Switch > img {
    width: 28px;
    height: 28px;
    transition: 0.2s;
    
}
.Switch__active {
    border-color: #15426d;
    width: 32px;
}
.Switch__active > img {
    background-color: #15426d;
    opacity: 0;
}